import { Moon, Sparkles, Sun, Trash2 } from "lucide-react";
import {
  SignInButton,
  SignOutButton,
  SignUpButton,
  SignedIn,
  SignedOut,
} from "@clerk/nextjs";
import { Button } from "@/components/ui/button";

const Header = ({ isDarkMode, setIsDarkMode, handleClearChat }: any) => {
  return (
    <div
      className={`border-b px-6 py-4 flex items-center justify-between backdrop-blur-sm transition-colors duration-300 ${
        isDarkMode
          ? "bg-gray-900/90 border-gray-700"
          : "bg-white/80 border-gray-200"
      }`}
    >
      <div className="flex items-center space-x-4">
        <div className="relative">
          <div className="w-11 h-11 bg-gradient-to-r from-blue-500 via-purple-600 to-pink-500 rounded-full flex items-center justify-center shadow-md">
            <Sparkles className="w-6 h-6 text-white" />
          </div>
          <div className="absolute -bottom-0.5 -right-0.5 w-3.5 h-3.5 bg-green-500 rounded-full border-2 border-white dark:border-gray-900"></div>
        </div>

        <div>
          <h2 className={`text-lg font-bold ${isDarkMode ? "text-white" : "text-gray-800"}`}>
            AI Assistant
          </h2>
          <div className="flex items-center space-x-1.5">
            <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
            <span className={`text-sm ${isDarkMode ? "text-gray-400" : "text-gray-600"}`}>
              Online
            </span>
          </div>
        </div>
      </div>

      <div className="flex items-center gap-3">
        <button
          onClick={() => setIsDarkMode(!isDarkMode)}
          className={`p-2 rounded-lg ${
            isDarkMode ? "bg-gray-800 text-yellow-300" : "bg-gray-100 text-gray-700"
          }`}
        >
          {isDarkMode ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
        </button>

        <button
          onClick={handleClearChat}
          className={`p-2 rounded-lg ${
            isDarkMode ? "bg-gray-800 text-gray-300" : "bg-gray-100 text-gray-700"
          }`}
        >
          <Trash2 className="w-5 h-5" />
        </button>

        <div className="flex items-center gap-2">
          <SignedOut>
            <SignInButton mode="modal">
              <Button variant="ghost">Sign In</Button>
            </SignInButton>

            <SignUpButton mode="modal">
              <Button>Sign Up</Button>
            </SignUpButton>
          </SignedOut>

          <SignedIn>
            <SignOutButton>
              <Button variant="outline">Sign Out</Button>
            </SignOutButton>
          </SignedIn>
        </div>
      </div>
    </div>
  );
};

export default Header;
